/* src/Style/TaskForm.css */

.task-form-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 30px;
    background: #f9fafb;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.form-title {
    font-size: 2em;
    text-align: center;
    color: #007bff;
    margin-bottom: 25px;
}

.task-section, .plugin-section, .theme-section {
    padding: 20px;
    margin-bottom: 20px;
    background: #e3f2fd;
    border-radius: 8px;
}

h2 {
    color: #0056b3;
    font-size: 1.5em;
    margin-bottom: 15px;
    text-align: left;
}

label {
    display: flex;
    flex-direction: column;
    font-weight: bold;
    color: #333;
    margin-bottom: 15px;
}

input[type="text"], select, textarea {
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ced4da;
    border-radius: 5px;
    font-size: 1em;
    color: #495057;
    background-color: #fff;
    width: 100%;
    box-sizing: border-box;
}

textarea {
    resize: vertical;
    min-height: 80px;
}

button {
    font-size: 1em;
    font-weight: bold;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.connect-wallet-btn {
    background-color: #007bff;
    color: #fff;
    margin-top: 15px;
    width: 100%;
    border: none;
}

.connect-wallet-btn.connected {
    background-color: #28a745;
}

.connect-wallet-btn:hover, .publish-btn:hover {
    background-color: #0056b3;
}

.publish-btn {
    background-color: #007bff;
    color: #fff;
    width: 100%;
    border: none;
    margin-top: 15px;
}

@media (min-width: 768px) {
    .task-section, .plugin-section, .theme-section {
        display: flex;
        flex-direction: column;
    }

    label {
        margin-bottom: 20px;
    }
}
