:root {
    /* Color Palette */
    --primary-color: #4a90e2;
    --secondary-color: #50b3d2;
    --accent-color: #f78ca0;
    --background-color: #f4f6f9;
    --text-color: #333333;
    --card-background: white;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: var(--background-color);
}

.about-container {
    color: var(--text-color);
    margin: 20px;
}

/* Banner Style */
.banner {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: white;
    text-align: center;
    padding: 60px 20px;
    border-radius: 10px;
    margin-bottom: 30px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    animation: fadeIn 1s ease-in-out;
}

.banner-title {
    font-size: 2.5em;
    margin-bottom: 10px;
}

.banner-subtitle {
    font-size: 1.3em;
    font-weight: 300;
}

/* Card Style for Sections */
.card {
    background-color: var(--card-background);
    border-radius: 10px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s, box-shadow 0.2s;
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15);
}

/* Section Headers */
.about-section h2,
.features-section h2,
.long-description h2,
.tech-section h2 {
    color: var(--primary-color);
}

/* Feature Styles */
.feature {
    background: #e7f3ff;
    padding: 15px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: inset 0px 2px 5px rgba(0, 0, 0, 0.1);
    animation: fadeInUp 0.5s ease-in-out;
}

.feature h3 {
    margin-top: 0;
    color: var(--secondary-color);
}

/* Footer Banner Style */
.footer-banner {
    background: linear-gradient(135deg, var(--accent-color), var(--primary-color));
    color: white;
    text-align: center;
    padding: 40px 20px;
    margin-top: 40px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    animation: fadeIn 1s ease-in-out;
}

.footer-title {
    font-size: 1.8em;
    margin-bottom: 10px;
}

.footer-text {
    font-size: 1.2em;
    font-weight: 300;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes fadeInUp {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}
