body {
    background-color: #e9ecef; /* Light background for the whole page */
  }
  
  .container {
    max-width: 600px; /* Set max width for the form */
  }
  
  .card {
    border-radius: 15px; /* Rounded corners for the card */
  }
  
  h2 {
    font-weight: bold;
  }
  
  .btn {
    transition: background-color 0.3s ease; /* Smooth transition for button hover */
  }
  
  .btn-primary {
    background-color: #007bff; /* Primary color */
  }
  
  .btn-primary:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  
  input {
    border: 2px solid #007bff; /* Custom border color for inputs */
  }
  
  input:focus {
    border-color: #0056b3; /* Darker blue on focus */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Soft blue glow on focus */
  }
  
  label {
    font-weight: bold; /* Bold labels for better readability */
  }
  