:root {
    /* Color Palette */
    --primary-color: #4a90e2;
    --secondary-color: #50b3d2;
    --background-color: #f4f6f9;
    --text-color: #333;
    --accent-color: #f78ca0;
    --card-bg: white;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: var(--background-color);
    margin: 0;
}

.creator-container {
    text-align: center;
    padding: 20px;
    color: var(--text-color);
}

.creator-title {
    font-size: 2.2em;
    margin-bottom: 20px;
    color: var(--primary-color);
}

/* Quest Card */
.quest-card {
    background: var(--card-bg);
    padding: 20px;
    margin: 20px auto;
    max-width: 500px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
}

.quest-card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15);
}

.quest-title {
    font-size: 1.8em;
    color: var(--primary-color);
}

.quest-description {
    margin-top: 10px;
    font-size: 1.1em;
    color: var(--text-color);
}

.button-group {
    margin-top: 20px;
    display: flex;
    gap: 10px;
    justify-content: center;
}

.accept-btn, .reject-btn, .details-btn {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 1em;
    cursor: pointer;
    transition: background 0.3s ease;
}

.accept-btn {
    background-color: var(--primary-color);
    color: white;
}

.reject-btn {
    background-color: #f05454;
    color: white;
}

.details-btn {
    background-color: var(--secondary-color);
    color: white;
}

/* Quest Details Modal */
.quest-details-modal {
    background: var(--card-bg);
    padding: 20px;
    max-width: 450px;
    margin: 20px auto;
    border-radius: 10px;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
    animation: fadeIn 0.5s ease;
}

.quest-details-modal h3 {
    color: var(--primary-color);
    margin-bottom: 15px;
}

.quest-details-modal p {
    font-size: 1.1em;
    color: var(--text-color);
    margin: 10px 0;
}

.close-details-btn {
    background-color: var(--primary-color);
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Acceptance Popup */
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: fadeIn 0.3s ease;
}

.popup-content {
    background: var(--card-bg);
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
}

.popup-content h2 {
    color: var(--accent-color);
    margin-bottom: 15px;
}

.popup-content p {
    font-size: 1.1em;
    margin-bottom: 20px;
    color: var(--text-color);
}

.close-popup-btn {
    background-color: var(--primary-color);
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}
