/* src/styles/Results.css */
.results-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.results-header {
  text-align: center;
  margin-bottom: 20px;
}

.results-header h2 {
  font-size: 2em;
  color: #333;
}

.results-header p {
  color: #666;
}

.winners-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 800px;
  margin-bottom: 20px;
}

.winner-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  transition: transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.winner-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.winner-card h3 {
  font-size: 1.5em;
  color: #007bff;
}

.winner-card p {
  color: #333;
  margin: 10px 0;
}

.btn-primary {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-primary:hover {
  background-color: #0056b3;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  background: #fff;
  padding: 30px;
  border-radius: 8px;
  text-align: center;
}

.popup p {
  font-size: 1.2em;
  color: #333;
}

.btn-light {
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  color: #333;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.btn-light:hover {
  background-color: #e2e2e2;
}
