body {
    background-color: #e9ecef; /* Light background for the whole page */
  }
  
  .container {
    max-width: 400px; /* Set max width for the form */
  }
  
  .card {
    border-radius: 15px; /* Rounded corners for the card */
  }
  
  .btn {
    transition: background-color 0.3s ease; /* Smooth transition for button hover */
  }
  
  .btn-primary:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  
  .btn-success {
    background-color: #28a745; /* Green color for Sign Up button */
  }
  
  .btn-success:hover {
    background-color: #218838; /* Darker green on hover */
  }
  